import { inject, noView } from 'aurelia-framework';
import { http } from 'resources/http';

@noView
@inject(http)
export class Posts {
    constructor(http) {
        this.httpClient = http;
        this.urls = [
            "src/img/slide-3.jpg",
            "src/img/slide-2.jpg",
            "src/img/slide-1.jpg"
        ];

        this.posts = [];
        this.displayedPosts = [];
        this.fetchPosts = this.httpClient.fetch('https://jsonplaceholder.typicode.com/posts', {cache: "force-cache"}); // dummy data
        this.fetchPosts
            .then((response => {
                if (response.status !== 200) {
                    console.log(`Something went wrong. Status: ${response.status}`);
                    return
                }

                response.json().then(x => {
                    this.posts = x;

                    // add dates and urls to our dummy data
                    this.posts.map( x => {
                        x.date = this.randomDate(new Date(2017, 0, 1), new Date());
                        x.url  = this.urls[Math.floor(Math.random() * (2 + 1))];

                    });

                    this.posts.sort((a, b) => b.date - a.date);
                    this.displayedPosts = this.posts.slice(0, 4);
                });

            }));
    }

    // helper for dummy data
    randomDate(start, end) {
        return new Date(start.getTime()
            + Math.random()
            * (end.getTime() - start.getTime()));
    }
}